import { Component } from '@angular/core';
import { Router } from '@angular/router';
import { ApiService } from '../services/api.service'; // Asegúrate de que esta ruta coincide con tu proyecto

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.css'],
})
export class LoginComponent {
  loginData = {
    username: '',
    password: '',
  };

  errorMessage: string | null = null;

  constructor(private apiService: ApiService, private router: Router) {}

  onLogin() {
    this.errorMessage = null; // Reiniciar mensajes de error
    this.apiService.login(this.loginData).subscribe({
      next: (response) => {
        // Guardar token o cualquier dato necesario en localStorage
        localStorage.setItem('authToken', response.token);
        localStorage.setItem('username', response.username);
        localStorage.setItem('role', response.role);

        // Redirigir al dashboard o página principal
        this.router.navigate(['/dashboard']);
      },
      error: (err) => {
        console.error('Error al iniciar sesión:', err);
        this.errorMessage = 'Credenciales inválidas. Por favor, inténtalo de nuevo.';
      },
    });
  }
}
